body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@primaryColor: #5BBFBA;@secondaryColor: #5F6CAF;@dark: #222;@textColor: #111;@linkColor: #61dafb;@green: #A4D4AE;@orange: #F0CF85;@yellow: #E7F0C3;@darkGray: #444;@midGray: #777;@lightGray: #aaa;@slateGray: #30404d;@primaryGrad: linear-gradient(45deg, #5BBFBA, #5F6CAF);@secondaryGrad: linear-gradient(45deg,#5F6CAF,white);