@import "./colors.less";

.App {
  text-align: center;
}

.App-header {
  background-color: @background;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: @textColor;
}

.App-link {
  color: @linkColor;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;

  @media (prefers-reduced-motion: no-preference) {
    animation: App-logo-spin infinite 20s linear;
  }
}

.calliope-list-item {
  padding: 0.5em;
}

.calliope-post {
  padding-left: 0.5em;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@primaryColor: #5BBFBA;@secondaryColor: #5F6CAF;@dark: #222;@textColor: #111;@linkColor: #61dafb;@green: #A4D4AE;@orange: #F0CF85;@yellow: #E7F0C3;@darkGray: #444;@midGray: #777;@lightGray: #aaa;@slateGray: #30404d;@primaryGrad: linear-gradient(45deg, #5BBFBA, #5F6CAF);@secondaryGrad: linear-gradient(45deg,#5F6CAF,white);